import mixins from "../../../utils/mixins";
import LayoutLr from "../../../components/Layout/LayoutLr";
import MySelect from "../../../components/MySelect";
import DrawerLayoutRight from "../../../components/utils/dialog/DrawerLayoutRight";
import MyElTree from "../../../components/tree/MyElTree";
import MyPagination from "../../../components/MyPagination";
import CircleChart from "../../../components/charts/CircleChart";
import DialogNormal from "../../../components/utils/dialog/DialogNormal";
import MyInputButton from "../../../components/search/MyInputButton";
import TableBarChart from "../../../components/charts/TableBarChart";
import MyDatePicker from "../../../components/MyDatePicker";
import MyHeadImg from "../../../components/utils/common/MyHeadImg";
import {common} from "../../../utils/api/url";
import {MessageError, MessageSuccess, MessageWarning} from "../../../utils/utils";
import rpApplyValidater from "../../../utils/validater/rpApplyValidater";
export default {
  mixins: [mixins, rpApplyValidater],
  components: {LayoutLr,MyElTree,MySelect,DrawerLayoutRight,MyPagination,CircleChart,DialogNormal,MyInputButton,TableBarChart,MyDatePicker,MyHeadImg},
  data(){
    return {
      toggleTopShow: false,
      pageStudent: 1,
      numStudent: 20,
      totalStudent: 0,
      dataAudit: {},
      drawerVisible: false,
      modalVisible: false,
      dialogLoading: false,
      visibleConfim: false,
      studentLoading: false,
      currentTotal: 0,
      successTotal: 0,
      subTitle: '',
      tableData: [],
      studentData: [],
      dataAuditList: [],
      searchCollege: '',
      searchMajor: '',
      searchGrade: '',
      searchClass: '',
      applyTimeBegin: '',
      applyTimeEnd: '',
      searchTime: '',
      type: '',
      level: '',
      status: '',
      searchKey: '',
      searchDate: [],
      //filterTypes: [{ text: this.$t("处分"), value: '处分' }, { text: '奖励', value: '奖励' }],
      filterLevels: [],
      filterAddLevels: [],
      //filterStatus: [{ text: this.$t("待审批"), value: '0' }, { text: '已通过', value: '3' }, { text: '已驳回', value: '4' }],
      personTotal: 0,
      reTotal: 0,
      puTotal: 0,
      uploadFileAction: common.upload_file,
      typeData: 0,
      typeListData: [],
      typeDataKey: [],
      levelData: [],
      levelDataKey: [],
      searchStudentKey: '',
      errorStudent: '',
      userId: '',
      filterScoreLevelsText: ''
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        collegeId: this.searchCollege,
        majorId: this.searchMajor,
        grade: this.searchGrade,
        clasz: this.searchClass,
        standard: this.status,
        searchKey: this.searchKey
      };
      this.$axios.get(common.audit_credit_static_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;

          this.initStatic();
        }
      });
    },
    initStatic(){
      let params = {
        collegeId: this.searchCollege,
        majorId: this.searchMajor,
        grade: this.searchGrade,
        clasz: this.searchClass
      };
      this.$axios.get(common.audit_credit_static_count, {params: params}).then(res => {
        if (res.data.data){

          let levelKey = [];
          let levelList = [];
          for (let i = 0; i < res.data.data.list.length; i++){
            levelKey.push(res.data.data.list[i].name);
            levelList.push({
              name: res.data.data.list[i].name,
              value: res.data.data.list[i].count,
              rate: res.data.data.list[i].rate,
            });
          }
          this.typeData = res.data.data.rate;
          this.personTotal = res.data.data.count;
          this.levelData = levelList;
          this.levelDataKey = levelKey;
        }
      });
    },
    initScore(){
      let params = {
        page: this.pageStudent,
        num: this.numStudent,
        applyTypeCode: 'ScoreApply',
        userId: this.userId,
        applyTimeBegin: this.searchTime
      };
      this.$axios.get(common.audit_page, {params: params}).then(res => {
        if (res.data.code == 200){
          this.studentData = res.data.data.list;
          this.totalStudent = res.data.data.totalCount;
          this.numStudent = res.data.data.num;
          this.pageStudent = res.data.data.currentPage;
        }else {
          MessageWarning(res.data.desc);
        }
      });
    },
    nodeClick(data){
      this.searchCollege = "";
      this.searchMajor = "";
      this.searchGrade = "";
      this.searchClass = "";
      if (data.unit == 1){
        this.searchCollege = data.id;
      }else if (data.unit == 2){
        this.searchCollege = data.college_id;
        this.searchMajor = data.id;
      }else if (data.unit == 3){
        this.searchMajor = data.major_id;
        this.searchGrade = data.grade;
      }else if (data.unit == 4){
        this.searchClass = data.id;
      }
      this.page = 1;
      this.init();
    },
    handleDetail(row){
      this.userId = row.user_id;
      this.currentTotal = row.quality_score;
      this.successTotal = row.scoreLevel;
      this.initScore();
      this.drawerVisible = true;
    },
    closeDrawerDialog(event){
      this.searchTime = "";
      this.drawerVisible = event;
    },
    cancelDrawDialog(){
      this.drawerVisible = false;
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    sizeStudentChange(event){
      this.pageStudent = 1;
      this.numStudent = event;
      this.initScore();
    },
    currentStudentPage(event){
      this.pageStudent = event;
      this.initScore();
    },
    search(data){
      this.searchKey = data.input;
      this.page = 1;
      this.init();
    },
    expandInfo(){
      let url = common.audit_credit_export;
      let params = {
        page:1,
        num:99999,
        collegeId: this.searchCollege,
        majorId: this.searchMajor,
        grade: this.searchGrade,
        clasz: this.searchClass,
        standard: this.status,
        searchKey: this.searchKey
      };
      params = this.$qs.stringify(params);
      window.open(url+"?"+params, "_self");
    },
    expandStudentInfo(){
      let url = common.audit_download;
      let params = {
        page:1,
        num:99999,
        applyTypeCode: 'ScoreApply',
        userId: this.userId,
        collegeId: this.searchCollege,
        majorId: this.searchMajor,
        grade: this.searchGrade,
        clasz: this.searchClass,
        applyTimeBegin: this.searchTime
      };
      params = this.$qs.stringify(params);
      window.open(url+"?"+params, "_self");
    },
    cancelDialog(){
      this.modalVisible = false;
    },
    closeDialog(event){
      this.pageStudent = 1;
      this.numStudent = 20;
      this.searchStudentKey = "";
      if (this.$refs.studentRef){
        this.$refs.studentRef.inputValue = "";
      }
    },
    fliterTable(value, row, column){
      for (let item in value){
        if (item == 'level'){
          this.filterScoreLevelsText = "";
          this.status = value[item][0];
          for (let i = 0; i < this.filterScoreLevels.length; i++){
            if (this.status == this.filterScoreLevels[i].value){
              this.filterScoreLevelsText = this.filterScoreLevels[i].text;
            }
          }
        }
      }
      this.page = 1;
      this.init();
    },
    handleTime(data){
      this.searchTime = data;
    },
    searchStudent(){
      this.pageStudent = 1;
      this.initScore();
    }
  }
}
